import React from "react"
import { Link } from "react-router-dom";

export default class EmbedViewerOnSquarespace extends React.Component {
    render() {
        return (
            <div className='help-page'>
                <h1>Integrating the viewer with your Squarespace site</h1>
                <h2>Add the viewer to a product page</h2>
                <h4>1. Add a Code block to the product</h4>
                <ol>
                    <li>Navigate to the product page and select Edit Product in the dropdown in the top left corner.</li>
                    <img alt='wix products' src={require('assets/images/help/squarespace-edit-product.jpg')} />
                    <li>Click Additional info, a window will appear. Highlight the text section and click the add button.</li>
                    <li>From the dialog that appears select Code.</li>
                    <img alt='wix products' src={require('assets/images/help/squarespace-add-code.jpg')} />
                </ol>
                <h4>2. Add the viewer iframe code</h4>
                <ol>
                    <li>Navigate to the <Link to='/embed'>integrate viewer page</Link> and choose the required settings.</li>
                    <li>In the HTML code section set the desired width and aspect ratio of the viewer window.</li>
                    <li>Select your project in the dropdown.</li>                
                    <img alt='wix products' src={require('assets/images/help/squarespace-embed.jpg')} />
                    <li>Click the Copy button and paste the code into your Code block.</li>
                    <img alt='wix products' src={require('assets/images/help/squarespace-paste-code.jpg')} />
                </ol>
                The 3D Web Viewer should appear on the page.
                <h2>Add the viewer to other pages</h2>
                <p>
                    Rather than adding the Code block to the product as above, add it to your page when in design mode.
                </p>
            </div>
        );
    }
}

